.feature-box {
  background-color: rgba($color-white, 0.8);
  font-size: 1.5rem;
  padding: 2.5rem;
  text-align: center;
  border-radius: 3px;
  box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);
  margin-right: 6rem;
  transition: transform 0.3s;

  @include respond(tab-port) {
    width: 45%;
    flex-flow: wrap;
    margin-right: 0;
    margin-bottom: 3rem;
    padding: 2rem;
  }

  &--heart {
    margin-right: 0;
  }

  &__icon {
    display: inline-block;
    font-size: 6rem;
    margin-bottom: 0.5rem;
    background-image: linear-gradient(
      to right,
      $color-primary-light,
      $color-primary-dark
    );
    background-clip: text;
    color: transparent;

    @include respond(tab-port) {
      margin-bottom: 0;
    }
  }

  &:hover {
    transform: translateY(-1.5rem) scale(1.03);
  }
}

.features__container {
  display: flex;
  justify-content: center;
  max-width: 114rem;
  margin: 0 auto;

  @include respond(tab-port) {
    max-width: 90rem;
    flex-flow: wrap;
    justify-content: space-evenly;

    .features__container:nth-child(2n) {
      margin-right: 2rem;
    }
  }
}
